import React from 'react'
import '../CssFolder/homepage.css'
import ImageSlider from './ImageSlider'
import HomeViewCard from './HomeViewCard'

function HomePage() {


    return (
        <div className="homepageMainDiv" >

            <div className="animate1">
                <img className="home_img1"
                    src="https://static.dribbble.com/users/1207339/screenshots/7008051/schronisko_01_1x.gif"
                />
                <div className="txtDiv1">
                    <p className="home_title">Craft Mania</p>
                    <p className="home_txt1">Our vision is to get each and every craft supply to you at your doorstep with the best prices and services and make crafting super fun for you all.
We are also trying to get the best support system for all the crafters which will give us all a very big platform for crafting.</p>
                </div>

                <button className="home_btn1">View Latest Collection</button>
            </div>
            <p className="my_work_p">My work : </p>

            <HomeViewCard />

        </div>
    )
}

export default HomePage
