import React from 'react'
import '../CssFolder/card1.css'

function Card1(props) {
    return (
        <div className="card1_mainDiv">
            <img className="card1_img" src={props.imgUrl}/>
            <p className="card1_title">{props.title}</p>
            <p className="card1_description">{props.description}</p>
        </div>
    )
}

export default Card1
